<template>
  <div class="section">
    <div class="container">
      <div class="text" v-if="propertyState">
        <p class="font-size-semi-large font-weight-bold mb-2">
          {{ $t('form.propertyDescription') }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <span class="light-gray font-weight-bold">
          {{ propertyState.description_en }}
        </span>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property description">
      <div class="form-input" v-if="propertyState">
        <label>
          {{ $t('form.propertyDescription') }}
        </label>
        <b-form-input
          :placeholder="$t('form.writeSuitableDescription')"
          v-model="updated.description_en"
          :state="state"></b-form-input>
        <b-form-invalid-feedback>
          Please provide a valid description
        </b-form-invalid-feedback>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave" :disabled="!state">
            Save
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      updated: {
        description_en: '',
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    state() {
      if (!this.updated?.description_en) return null;
      return this.updated?.description_en.trim().length > 0;
    },
  },
  methods: {
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        description_en: this.updated.description_en,
      });
      this.show = false;
    },
    cancel() {
      this.updated.description_en = this.propertyState.description_en;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.description_en = this.propertyState.description_en;
    }
  },
};
</script>

<style lang="scss"></style>
