<template>
  <div class="form-heading" v-if="propertyState">
    <h2>
      {{ propertyState && propertyState.property_name }}
    </h2>
    <b-button variant="link" @click="show = !show">
      <svg-icon name="edit" width="20" height="20"></svg-icon>
      {{ $t('common.edit') }}
    </b-button>

    <b-modal v-model="show" centered title="Update property name">
      <div class="form-input" v-if="propertyState">
        <label>
          {{ $t('form.PropertyNameTitle') }}
        </label>
        <b-form-input
          :placeholder="$t('form.PropertyNameTitle1')"
          v-model="updated.property_name"
          :state="state"></b-form-input>
        <b-form-invalid-feedback>
          Please provide a valid name
        </b-form-invalid-feedback>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave" :disabled="!state">
            Save
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      updated: {
        property_name: '',
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    state() {
      if (!this.updated?.property_name) return null;
      return this.updated?.property_name?.trim().length > 0;
    },
  },
  methods: {
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        property_name: this.updated.property_name,
      });
      this.show = false;
    },
    cancel() {
      this.updated.property_name = this.propertyState.property_name;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.property_name = this.propertyState.property_name;
    }
  },
};
</script>

<style lang="scss"></style>
