<template>
  <div v-if="propertyState && propertyState.amenities">
    <div
      class="section"
      v-for="(amenityType, index) in Object.keys(propertyState.amenities)"
      :key="index">
      <div class="container">
        <p class="font-size-semi-large font-weight-bold">
          {{ $t(`form.${amenityType.toLowerCase()}Amenities`) }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <span
          class="chip"
          v-for="amenity in propertyState.amenities[amenityType]"
          :key="amenity.id">
          {{ amenity.amenity ? amenity.amenity : amenity.amenity_name }}
        </span>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property amenities">
      <div class="form-input" v-if="propertyState && propertyState.amenities">
        <label>
          {{ $t('form.bathroomAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updateBathroomAmenities"
          :options="bathroomOptions"></ChipSelector>
      </div>
      <div class="form-input" v-if="propertyState && propertyState.amenities">
        <label>
          {{ $t('form.kitchenAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updateKitchenAmenities"
          :options="kitchenOptions"></ChipSelector>
      </div>
      <div class="form-input" v-if="propertyState && propertyState.amenities">
        <label>
          {{ $t('form.PropertyAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updatePropertyAmenities"
          :options="propertyOptions"></ChipSelector>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave"> Save </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import ChipSelector from '@/components/shared/form/chipSelector';

export default {
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    ChipSelector,
  },
  data() {
    return {
      updated: {
        amenities: {},
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    propertyOptions() {
      return this.propertyData?.amenities?.Property;
    },
    kitchenOptions() {
      return this.propertyData?.amenities?.Kitchen;
    },
    bathroomOptions() {
      return this.propertyData?.amenities?.Bathroom;
    },
  },
  methods: {
    updatePropertyAmenities(item) {
      const currentItems = this.propertyData.amenities.Property.filter(
        (amenity) => item.some((it) => it === amenity.amenity_id)
      );
      this.updated.amenities.Property = currentItems;
    },
    updateKitchenAmenities(item) {
      const currentItems = this.propertyData.amenities.Kitchen.filter(
        (amenity) => item.some((it) => it === amenity.amenity_id)
      );
      this.updated.amenities.Kitchen = currentItems;
    },
    updateBathroomAmenities(item) {
      const currentItems = this.propertyData.amenities.Bathroom.filter(
        (amenity) => item.some((it) => it === amenity.amenity_id)
      );
      this.updated.amenities.Bathroom = currentItems;
    },
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        amenities: this.updated.amenities,
      });
      this.show = false;
    },
    cancel() {
      this.updated.amenities = this.propertyState.amenities;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.amenities = this.propertyState.amenities;
    }
  },
};
</script>

<style lang="scss"></style>
