<template>
  <div class="address section">
    <div class="container">
      <div class="svg-icon">
        <svg-icon
          fill="#262D3D"
          name="dollar"
          height="24"
          width="24"></svg-icon>
      </div>
      <div class="text">
        <p class="font-size-semi-large font-weight-bold mb-0">
          {{ $t('property.propertyPrice') }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <p v-if="propertyState && propertyState.daily_price" class="my-2">
          <span class="font-weight-bold font-size-semi-large">
            {{ propertyState.daily_price }}
          </span>
          /{{ $t('form.shortTerm') }}
        </p>
        <p v-if="propertyState && propertyState.monthly_price">
          <span class="font-weight-bold font-size-semi-large">
            {{ propertyState.monthly_price }}
          </span>
          /{{ $t('form.longTerm') }}
        </p>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property price">
      <div class="form-input">
        <div class="mb-3" v-if="propertyState">
          <label class="d-block mt-3">
            {{ $t('form.pricePerNight') }}
          </label>
          <b-form-input
            type="number"
            :placeholder="`${$t('form.moneyPlaceholder')} ${$t(
              'form.shortTerm'
            )}`"
            v-model="updated.daily_price"
            :state="dailyState"></b-form-input>
          <b-form-invalid-feedback>
            Daily price must be a valid amount
          </b-form-invalid-feedback>
        </div>
        <div v-if="propertyState">
          <label class="d-block mt-1">
            {{ $t('form.pricePerMonth') }}
          </label>
          <b-form-input
            type="number"
            :placeholder="`${$t('form.moneyPlaceholder')} ${$t(
              'form.longTerm'
            )}`"
            v-model="updated.monthly_price"
            :state="monthlyState"></b-form-input>
          <b-form-invalid-feedback>
            Monthly price must be a valid amount
          </b-form-invalid-feedback>
        </div>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button
            variant="primary"
            @click="onSave"
            :disabled="!dailyState && !monthlyState">
            Save
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
export default {
  data() {
    return {
      updated: {
        daily_price: 0,
        monthly_price: 0,
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    dailyState() {
      if (!this.updated?.daily_price) return null;
      return this.updated?.daily_price >= 0;
    },
    monthlyState() {
      if (!this.updated?.monthly_price) return null;
      return this.updated?.monthly_price >= 0;
    },
  },
  methods: {
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        daily_price: this.updated.daily_price,
        monthly_price: this.updated.monthly_price,
      });
      this.show = false;
    },
    cancel() {
      this.updated.daily_price = this.propertyState.daily_price;
      this.updated.monthly_price = this.propertyState.monthly_price;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.daily_price = this.propertyState.daily_price;
      this.updated.monthly_price = this.propertyState.monthly_price;
    }
  },
};
</script>

<style lang="scss"></style>
