<template>
  <b-overlay :show="loading" rounded="sm" :opacity="0.5">
    <div class="edit-property">
      <div class="container">
        <FormContainer :propertyData="propertyData" />
      </div>
    </div>
  </b-overlay>
</template>

<script>
import FormContainer from '@/components/dashboard/editProperty/FormContainer';
import { fetchPropertiesData } from '@/api/properties';
export default {
  name: 'EditPropertyPage',
  components: {
    FormContainer,
  },
  data() {
    return {
      propertyData: {},
      loading: false,
    };
  },
  created() {
    this.loading = true;
  },
  async mounted() {
    this.loading = true;
    const { Data } = await fetchPropertiesData();
    this.propertyData = Data;

    const { Data: propertyDetails } = await this.$axios.$get(
      `/user/properties/${this.$route.params.propertyId}`
    );

    if (propertyDetails)
      this.$store.commit(
        'PropertyModule/SET_UPDATED_PROPERTY',
        propertyDetails
      );
    this.loading = false;
  },
};
</script>

<style lang="scss">
.edit-property {
  min-height: calc(100vh - 48px - 48px);
  background-color: #f7f9fa;
  padding: 48px 0;

  .container {
    height: 100%;

    .form {
      width: 700px;
      .form-container {
        width: 100%;
        height: 100%;
        padding: 32px;
        margin: 16px 0;
        border-radius: 16px;
        background-color: #fff;

        .form-heading {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin: 16px 0;

          h2 {
            color: #111;
            font-size: 24px;
            font-weight: bold;
          }
        }

        .form-input {
          padding: 18px 0;
          border-bottom: 1px solid #f2f4f5;

          &:last-child {
            border: none;
          }

          label {
            font-size: 16px;
            font-weight: bold;
          }

          .v-select {
            svg {
              all: unset;
            }
          }

          .chips-container {
            margin: 0;
          }

          .number-counter-input {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }

          input.form-input {
            padding: 24px 16px;
            border: 1px solid #e3e5e5;
            border-radius: 10px;
          }
        }
      }
    }

    .page-actions {
      display: flex;
      justify-content: center;
      align-items: center;

      .btn {
        width: 280px;
        padding: 12px 0;
        font-weight: bold;
        border-radius: 8px;
        border: none;
        margin: 8px;
        text-transform: capitalize;

        &-primary {
          color: #fff;
          background-color: #0078ff;
        }

        &-secondary {
          color: #111;
          background-color: #fff;
        }
      }
    }
  }
  .preview-form {
    .section {
      background-color: white;
      padding: 18px 0;
      border-bottom: 1px solid #f2f4f5;
      margin-top: 18px;

      p.font-size-semi-large {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      &:last-child {
        border: none;
        padding-bottom: 0;
      }

      &.address {
        .container {
          display: flex;
          align-items: flex-start;

          .text {
            width: 100%;
          }

          .svg-icon {
            margin: 5px 16px 0 16px;
          }
        }
      }

      .images {
        width: 100%;
        height: 100%;
        display: flex;
        overflow-y: hidden;
        overflow-x: scroll;
        padding: 8px 0;

        .image {
          margin-right: 12px;

          &:last-child {
            margin: 0;
          }

          img {
            border-radius: 12px;
            object-fit: cover;
          }
        }
      }

      .number-counter {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px 0;

        &:last-child {
          border: none;
        }

        p {
          margin: 0;
          font-weight: 600;
          font-size: 16px;
        }

        .number {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 40px;
          height: 40px;
          border: 1px solid #e3e5e5;
          border-radius: 50%;
        }
      }

      .chip {
        display: inline-block;
        padding: 8px 12px;
        background-color: #e6f2ff;
        color: #0078ff;
        font-weight: bold;
        border-radius: 32px;
        margin: 0px 12px 12px 0;

        &:last-child {
          margin: 0;
        }
      }
    }

    .btn-link {
      display: flex;
      justify-content: center;
      align-items: center;
      color: #111;
      font-size: 14px;
      text-decoration: none;
      text-transform: capitalize;

      svg {
        margin-right: 4px;
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .mobile-scroll {
    justify-content: flex-start;
    overflow-x: scroll;
  }
}

@media screen and (max-width: 768px) {
  .edit-property {
    .container {
      .container {
        padding: 0;
      }
      .form {
        width: 100%;
      }

      .page-actions {
        flex-direction: column-reverse;
      }
    }
  }
}
</style>
