<template>
  <div class="section">
    <div class="container">
      <div class="text" v-if="propertyState && propertyState.bedroom">
        <p class="font-size-semi-large font-weight-bold">
          {{ $t('form.noOfRooms') }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <div class="number-counter">
          <p>
            {{ $t('form.bedrooms') }}
          </p>
          <div class="number">
            <span>{{ propertyState.bedroom }}</span>
          </div>
        </div>
        <div class="number-counter">
          <p>
            {{ $t('property.bathrooms') }}
          </p>
          <div class="number">
            {{ propertyState.bathroom }}
          </div>
        </div>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property updated">
      <div class="form-input" v-if="propertyState && propertyState.bedroom">
        <label>
          {{ $t('property.room_numbers') }} <sup style="color: red">*</sup>
        </label>
        <div class="number-counter-input">
          <label>
            {{ $t('property.bedrooms') }}
          </label>
          <CustomCounter
            :step="1"
            :validation-name="$t('property.bedrooms')"
            validation-rules="required|min_value:1"
            v-model="updated.bedroom"
            :min="1"
            :max="10" />
        </div>
        <div class="number-counter-input">
          <label for="bathrooms1">
            {{ $t('property.bathrooms') }}
          </label>

          <CustomCounter
            id="bathrooms1"
            :step="1"
            :validation-name="$t('property.bathrooms')"
            validation-rules="required|min_value:1"
            v-model="updated.bathroom"
            :min="1"
            :max="10" />
        </div>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave"> Save </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import CustomCounter from '@/components/shared/form/CustomCounter';

export default {
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    CustomCounter,
  },
  data() {
    return {
      updated: {
        bedroom: 1,
        bathroom: 1,
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
  },
  methods: {
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        bathroom: this.updated.bathroom,
        bedroom: this.updated.bedroom,
      });
      this.show = false;
    },
    cancel() {
      this.updated.bedroom = this.propertyState.bedroom;
      this.updated.bathroom = this.propertyState.bathroom;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.bedroom = this.propertyState.bedroom;
      this.updated.bathroom = this.propertyState.bathroom;
    }
  },
};
</script>

<style lang="scss"></style>
