<template>
  <div class="section" v-if="propertyState && propertyState.old_images">
    <div class="container">
      <div class="text">
        <p class="font-size-semi-large font-weight-bold mb-2">
          {{ $t('form.propertyPhotos') }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <div class="images mobile-scroll">
          <template v-if="uploadedImages.length">
            <div
              class="image"
              v-for="(image, index) in uploadedImages"
              :key="index">
              <img :src="image" width="120" height="100" />
            </div>
          </template>
          <template>
            <div
              class="image"
              v-for="(image, index) in propertyState.old_images"
              :key="image + index">
              <img :src="image" width="120" height="100" />
            </div>
          </template>
        </div>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property images">
      <div class="form-input">
        <label>
          {{ $t('form.uploadPropertyphotos') }}
        </label>
        <UploadImages
          @changed="handleImages"
          :max="20"
          :uploadMsg="'Drag & Drop images here'"
          fileError="images files only accepted"
          clearAll="Remove all images" />
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button
            variant="primary"
            @click="onSave"
            :disabled="!uploadedImages?.length">
            Save
          </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import UploadImages from 'vue-upload-drop-images';
export default {
  components: {
    UploadImages,
  },
  data() {
    return {
      show: false,
      uploadedImages: [],
      files: null,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
  },
  methods: {
    async handleImages(files) {
      this.files = files;
      for (let i = 0; i < files.length; i++) {
        const buffer = Buffer.from(await new Response(files[i]).arrayBuffer());
        const dataUrl = `data:${files[i].type};base64,${buffer.toString(
          'base64'
        )}`;
        this.uploadedImages.push(dataUrl);
      }
    },
    onSave() {
      this.$emit('updateImages', this.files);
      this.show = false;
    },
    cancel() {
      this.uploadedImages = [];
      this.show = false;
    },
  },
};
</script>

<style lang="scss"></style>
