<template>
  <div class="section" v-if="propertyState && furnishType">
    <div class="container">
      <div class="text">
        <p class="font-size-semi-large font-weight-bold">
          {{ $t(`property.furnishing_status`) }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <span class="chip">
          {{ furnishType && furnishType.text }}
        </span>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property furnishing status">
      <div class="form-input" v-if="propertyState && furnishType">
        <label>
          {{ $t('property.furnishing_status') }} <sup style="color: red">*</sup>
        </label>
        <ChipSelector
          value="id"
          label="text"
          @update-selection="updateFurnishType"
          :options="propertyData.furnishType"></ChipSelector>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave"> Save </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import ChipSelector from '~/components/shared/form/chipSelector';
export default {
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    ChipSelector,
  },
  data() {
    return {
      updated: {
        furnish_type_id: '',
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    furnishType() {
      return (
        this.propertyData?.furnishType?.find(
          (item) => item.id === this.updated?.furnish_type_id
        ) || ''
      );
    },
  },
  methods: {
    updateFurnishType(item) {
      if (item && item.length > 0) this.updated.furnish_type_id = item[0];
    },
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        furnish_type_id: this.updated.furnish_type_id,
      });
      this.show = false;
    },
    cancel() {
      this.updated.furnish_type_id = this.propertyState.furnish_type_id;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.furnish_type_id = this.propertyState.furnish_type_id;
    }
  },
};
</script>

<style lang="scss"></style>
