<template>
  <div class="form container">
    <b-overlay :show="loading" opacity="0.5">
      <div class="preview-form">
        <div class="form-container">
          <!-- Property Name -->
          <UpdateName />

          <!-- Property Details -->
          <UpdatePrice />
          <UpdateAddress :propertyData="propertyData" />

          <!-- Property Description -->
          <UpdateDescription />

          <!-- Property Images -->
          <UpdatePhotos @updateImages="(items) => (newImages = items)" />

          <!-- Furnish Type -->
          <UpdateFurnishType :propertyData="propertyData" />

          <!-- Property Type -->
          <UpdatePropertyType :propertyData="propertyData" />

          <!-- Property Rooms -->
          <UpdateRooms :propertyData="propertyData" />
          <!-- Property Bed Types -->
          <!-- <UpdateBedTypes :propertyData="propertyData" /> -->

          <!-- Amenities -->
          <UpdateAmenities :propertyData="propertyData" />
        </div>

        <div class="page-actions">
          <b-button @click="goBack">{{ $t('common.back') }}</b-button>
          <b-button
            variant="primary"
            @click="updateProperty"
            :disabled="buttonLoading">
            {{ !buttonLoading ? $t('common.update') : 'Loading...' }}
          </b-button>
        </div>
      </div>
    </b-overlay>
  </div>
</template>

<script>
import UpdateName from './UpdateName';
import UpdatePrice from './UpdatePrice';
import UpdateAddress from './UpdateAddress';
import UpdateDescription from './UpdateDescription';
import UpdatePhotos from './UpdatePhotos';
import UpdateFurnishType from './UpdateFurnishType';
import UpdatePropertyType from './UpdatePropertyType';
import UpdateRooms from './UpdateRooms';
// // import UpdateBedTypes from './UpdateBedTypes';
import UpdateAmenities from './UpdateAmenities';

export default {
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    UpdateName,
    UpdatePrice,
    UpdateAddress,
    UpdateDescription,
    UpdatePhotos,
    UpdateFurnishType,
    UpdatePropertyType,
    UpdateRooms,
    // UpdateBedTypes,
    UpdateAmenities,
  },
  data() {
    return {
      newImages: null,
      loading: false,
      buttonLoading: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
  },
  methods: {
    async updateProperty() {
      try {
        this.buttonLoading = true;
        const data = this.convertDataForUpdate();

        await this.$axios.$post(
          `/user/properties/${this.$route.params.propertyId}?_method=PATCH`,
          data
        );

        this.buttonLoading = false;
        this.$bvToast.toast('Your property has been updated successfully!', {
          title: 'success',
          autoHideDelay: 3000,
          variant: 'success',
          appendToast: true,
        });
        this.$router.push(this.localePath('/dashboard/my-properties'));
      } catch (error) {
        this.buttonLoading = false;
        this.$bvToast.toast(error.response?.data?.Message, {
          title: 'Error',
          autoHideDelay: 3000,
          variant: 'danger',
          appendToast: true,
        });
      }
    },
    convertDataForUpdate() {
      const updatedProperty = this.propertyState;
      const bathroomAmenities = updatedProperty.amenities.Bathroom.map(
        (item) => item.id || item.amenity_id
      );
      const kitchenAmenities = updatedProperty.amenities.Kitchen.map(
        (item) => item.id || item.amenity_id
      );
      const propertyAmenities = updatedProperty.amenities.Property.map(
        (item) => item.id || item.amenity_id
      );

      let term = 0;
      if (updatedProperty.monthly_price && updatedProperty.daily_price)
        term = 2;
      else if (updatedProperty.monthly_price) term = 1;

      const bedTypes = updatedProperty.bed_types?.map((typ) => {
        return {
          property_bed_types_id: typ.id,
          count: typ.count,
        };
      });

      let convertedData = {
        name_en: updatedProperty.property_name,
        name_ar: updatedProperty.property_name_ar,
        description_en: updatedProperty.description_en,
        description_ar: updatedProperty.description_en,
        bathrooms: Number(updatedProperty.bathroom),
        bedrooms: Number(updatedProperty.bedroom),
        furnishType: Number(updatedProperty.furnish_type_id),
        propertyType: Number(updatedProperty.furnish_type_id),
        daily_price: Number(updatedProperty.daily_price),
        monthly_price: Number(updatedProperty.monthly_price),
        bed_types: bedTypes,
        term,
        amenities: [
          ...bathroomAmenities,
          ...kitchenAmenities,
          ...propertyAmenities,
        ],
        lat: updatedProperty.lat,
        lng: updatedProperty.lng,
        location_id: updatedProperty.location_id,
        default_image: updatedProperty.default_image,
        old_images: updatedProperty.old_images,
        google_location_en: updatedProperty.google_location_en,
        google_location_ar: updatedProperty.google_location_ar,
      };

      if (this.newImages) {
        convertedData = { ...convertedData, images: this.newImages };
      }
      // return convertedData;
      const formData = new FormData();
      if (this.newImages?.length) {
        for (var i in this.newImages)
          formData.append('images[' + i + ']', this.newImages[i]);
      }
      Object.keys(convertedData).forEach((index) => {
        if (Array.isArray(convertedData[index])) {
          this.appendArrayValue(formData, `${index}`, convertedData[index]);
        } else {
          formData.append(index, convertedData[index]);
        }
      });
      // for (var key in convertedData) {
      //   formData.append(key, convertedData[key]);
      // }
      return formData;
    },
    goBack() {
      this.$router.push(this.localePath('/dashboard/my-properties'));
    },
    appendArrayValue(formData, fieldName, array) {
      if (array.length === 0) {
        formData.append(`${fieldName}[]`, '');
        return;
      }
      array.forEach((item, index) => {
        if (typeof item === 'object' && !(item instanceof File)) {
          Object.keys(item).forEach((key) => {
            formData.append(`${fieldName}[${index}][${key}]`, item[key]);
          });
        } else {
          formData.append(`${fieldName}[${index}]`, item);
        }
      });
    },
  },
};
</script>

<style></style>
