<template>
  <div class="address section">
    <div class="container">
      <div class="svg-icon">
        <svg-icon
          fill="#262D3D"
          name="MapPin"
          height="24"
          width="24"></svg-icon>
      </div>
      <div class="text">
        <p class="font-size-semi-large font-weight-bold mb-0">
          {{ $t('form.PropertyAddress') }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <p>
          <span class="font-weight-bold">
            {{ propertyCity }}
          </span>
        </p>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property address">
      <div class="form-input">
        <label> {{ $t('common.city') }} <sup style="color: red">*</sup> </label>

        <rent-select
          :options="propertyData.rootLocations"
          id="citySelect"
          @input="cityInput"
          :title="$t('form.select_city')"
          :iconName="require(`@/assets/svg-sprite/svg/city.svg`)"
          :placeholder="$t('form.select_city')"
          label="location_name"
          value="location_id"
          class="destination-input" />
      </div>
      <div class="form-input my-4" v-if="districtOptions.length">
        <label>
          {{ $t('common.district') }}
        </label>
        <rent-select
          label="location_name"
          :options="districtOptions"
          @input="districtInput"
          :title="$t('form.select_district')"
          :iconName="require(`@/assets/svg-sprite/svg/district.svg`)"
          :placeholder="$t('form.select_district')">
        </rent-select>
      </div>
      <div
        class="form-input"
        v-if="districtOptions.length && neighborhoodOptions.length">
        <label>
          {{ $t('common.neighborhood') }}
        </label>
        <rent-select
          label="location_name"
          :options="neighborhoodOptions"
          @input="neighborhoodInput"
          :title="$t('form.choose_neighborhood')"
          :iconName="require(`@/assets/svg-sprite/svg/neighborhood.svg`)"
          :placeholder="$t('form.choose_neighborhood')">
        </rent-select>
      </div>
      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave"> Save </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import rentSelect from '~/components/shared/form/rentSelect';
export default {
  props: ['propertyData'],
  components: {
    rentSelect,
  },
  data() {
    return {
      updated: {
        location_id: '',
        location_name: '',
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    propertyCity() {
      if (this.propertyState?.location_id) {
        return (
          this.propertyData?.rootLocations?.find(
            (loc) => loc.location_id === this.propertyState.location_id
          )?.location_name || this.propertyState.location_name
        );
      }
      return null;
    },
    districtOptions() {
      return this.$store.state.PropertyModule.districtLocation;
    },
    neighborhoodOptions() {
      return this.$store.state.PropertyModule.neighborhoodLocation;
    },
  },
  methods: {
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        location_id: this.updated.location_id,
        location_name: this.updated.location_name,
      });
      this.show = false;
    },
    cancel() {
      this.updated.location_id = this.propertyState.description_en;
      this.show = false;
    },
    cityInput(city) {
      if (!city.location_id) return;

      this.updated.location_id = city.location_id;
      this.updated.location_name = city.location_name;

      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: city.location_id,
        type: 'district',
      });
    },
    districtInput(district) {
      if (!district?.location_id) return;

      this.updated.location_id = district.location_id;
      this.updated.location_name = district.location_name;
      this.$store.dispatch('PropertyModule/getChildRoot', {
        location_id: district.location_id,
        type: 'neighborhood',
      });
    },
    neighborhoodInput(neighborhood) {
      this.updated.location_id = neighborhood.location_id;
      this.updated.location_name = neighborhood.location_name;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.location_id = this.propertyState.location_id;
    }
  },
};
</script>

<style lang="scss"></style>
