<template>
  <div class="section" v-if="propertyType">
    <div class="container">
      <div class="text">
        <p class="font-size-semi-large font-weight-bold">
          {{ $t(`property.property_types`) }}
          <b-button variant="link" @click="show = !show">
            <svg-icon name="edit" width="20" height="20"></svg-icon>
            {{ $t('common.edit') }}
          </b-button>
        </p>
        <span class="chip">
          {{ propertyType && propertyType.prop_type_name }}
        </span>
      </div>
    </div>

    <b-modal v-model="show" centered title="Update property type">
      <div class="form-input" v-if="propertyType">
        <label>
          {{ $t('property.property_types') }}
        </label>
        <ChipSelector
          value="prop_type_id"
          label="prop_type_name"
          @update-selection="updatePropertyType"
          :options="propertyData.propertyType"></ChipSelector>
      </div>

      <template #modal-footer>
        <div class="actions">
          <b-button @click="cancel"> Cancel </b-button>
          <b-button variant="primary" @click="onSave"> Save </b-button>
        </div>
      </template>
    </b-modal>
  </div>
</template>

<script>
import ChipSelector from '~/components/shared/form/chipSelector';
export default {
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    ChipSelector,
  },
  data() {
    return {
      updated: {
        property_type_id: '',
      },
      show: false,
    };
  },
  computed: {
    propertyState() {
      return this.$store.state.PropertyModule.updatedProperty || null;
    },
    propertyType() {
      return (
        this.propertyData?.propertyType?.find(
          (item) => item.prop_type_id === this.updated?.property_type_id
        ) || ''
      );
    },
  },
  methods: {
    updatePropertyType(item) {
      if (item && item.length > 0) this.updated.property_type_id = item[0];
    },
    onSave() {
      this.$store.commit('PropertyModule/SET_UPDATED_PROPERTY', {
        ...this.propertyState,
        property_type_id: this.updated.property_type_id,
      });
      this.show = false;
    },
    cancel() {
      this.updated.property_type_id = this.propertyState.property_type_id;
      this.show = false;
    },
  },
  mounted() {
    if (this.propertyState) {
      this.updated.property_type_id = this.propertyState.property_type_id;
    }
  },
  updated() {
    if (this.propertyState) {
      this.updated.property_type_id = this.propertyState.property_type_id;
    }
  },
};
</script>

<style lang="scss"></style>
